<template>
	<ul class="catalog__pagination pagination">
		<li class="pagination__item">
			<a :class="{'pagination__link--disabled': 1 === page}" @click.prevent="1 === page || paginate(page - 1)" href="#" class="pagination__link pagination__link--arrow" aria-label="Предыдущая страница">
				<svg width="8" height="14" fill="currentColor">
					<use xlink:href="#icon-arrow-left"></use>
				</svg>
			</a>
		</li>
		<li class="pagination__item" v-for="pageNumber in pages">
			<a class="pagination__link" href="#" :class="{'pagination__link--current': pageNumber === page}" @click.prevent="paginate(pageNumber)">
				{{ pageNumber }}
			</a>
		</li>
		<li class="pagination__item">
			<a :class="{'pagination__link--disabled': pages === page}" @click.prevent="pages === page || paginate(page + 1)" href="#"  class="pagination__link pagination__link--arrow" aria-label="Следующая страница">
				<svg width="8" height="14" fill="currentColor">
					<use xlink:href="#icon-arrow-right"></use>
				</svg>
			</a>
		</li>
	</ul>

</template>

<script>
	export default {
		model: {
			prop: 'page',
			event: 'paginate'
		},
		props: ['page', 'count', 'perPage'],
		computed: {
			pages() {
				return Math.ceil(this.count / this.perPage);
			}
		},
		methods: {
			paginate(page) {
				this.$emit('paginate', page);
			}
		}
	}
</script>

