<template>
	<li class="cart__item product">
		<div class="product__pic">
			<img :src="item.product.image" width="120" height="120" :alt="item.product.title">
		</div>
		<h3 class="product__title">
			{{ item.product.title }}
		</h3>
		<span class="product__code">Артикул: {{ item.product.id }}</span>

		<ProductCounter class="product__counter" v-model="counter" />

		<b class="product__price">
			{{ item.product.price*item.amount|numberFormat }} ₽
		</b>

		<button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProduct(item.product.id)">
			<svg width="20" height="20" fill="currentColor">
				<use xlink:href="#icon-close"></use>
			</svg>
		</button>
	</li>
</template>

<script>
	import numberFormat from "../../helpers/numberFormat";
	import {mapActions} from "vuex";
	import ProductCounter from "@/components/ProductCounter.vue";

	export default {
		components: {ProductCounter},
		props: ['item'],
		filters: {numberFormat},
		methods: {
			...mapActions({
				deleteProduct: 'deleteCartProduct'
			})
		},
		computed: {
			counter: {
				get() {
					return this.item.amount;
				},
				set(value) {
					this.$store.dispatch('updateCartProductAmount', {productId: this.item.product.id, amount: value});
				}
			}
		}
	}
</script>
