<template>
	<div class="form__counter">
		<button type="button" aria-label="Убрать один товар" @click="increment(-1)">
			<svg width="10" height="10" fill="currentColor">
				<use xlink:href="#icon-minus"></use>
			</svg>
		</button>

		<input type="text" v-model.number="number" />

		<button type="button" aria-label="Добавить один товар" @click="increment(1)">
			<svg width="10" height="10" fill="currentColor">
				<use xlink:href="#icon-plus"></use>
			</svg>
		</button>
	</div>
</template>

<script>
	export default {
		model: {
			prop: 'counter',
			event: 'update'
		},
		props: ['counter'],
		methods: {
			increment(delta) {
				this.number += delta;
			}
		},
		computed: {
			number: {
				get() {
					return this.counter;
				},
				set(value) {
					this.$emit('update', Math.max(value, 1));
				}
			}
		}
	}
</script>
