<template>
	<main class="content container">
		<div class="content__top content__top--catalog">
			<h1 class="content__title">Каталог</h1>
			<span class="content__info">{{ countProducts }} товар(ов|а)</span>
		</div>
		<div class="content__catalog">
			<ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color-id.sync="filterColorId"/>
			<section class="catalog">
				<div v-if="productsLoading">Loading...</div>
				<div v-if="productsLoadingFailed">Ошибка при загрузке <button @click.prevent="loadProducts()">повторить ещё раз</button></div>
				<ProductList :products="products" />
				<BasePagination v-model="page" :perPage="productsPerPage" :count="countProducts" />
			</section>
		</div>
	</main>
</template>

<script>
	import ProductList from "@/components/ProductList";
	import BasePagination from "@/components/BasePagination.vue";
	import ProductFilter from "@/components/ProductFilter.vue";
	import axios from "axios";
	import {API_BASE_URL} from "@/config";

	export default {
		components: {ProductFilter, BasePagination, ProductList},
		data() {
			return {
				filterPriceFrom: 0,
				filterPriceTo: 12220,
				filterCategoryId: 0,
				filterColorId: 0,

				page: 1,
				productsPerPage: 3,

				productsData: null,
				productsLoading: false,
				productsLoadingFailed: false
			}
		},
		computed: {
			products() {
				return this.productsData ? this.productsData.items.map(product => {
					return {
						...product,
						image: product.image.file.url
					};
				}) : [];
				//const offset = (this.page - 1) * this.productsPerPage;
				//return this.filteredProducts.slice(offset, offset + this.productsPerPage);
			},
			countProducts() {
				return this.productsData ? this.productsData.pagination.total : 0;
			}
		},
		watch: {
			page() {
				this.loadProducts();
			},
			filterCategoryId() {
				this.loadProducts();
			},
			filterPriceFrom() {
				this.loadProducts();
			},
			filterPriceTo() {
				this.loadProducts();
			},
			filterColorId() {
				this.loadProducts();
			}
		},
		methods: {
			loadProducts() {
				this.productsLoading = true;
				this.productsLoadingFailed = false;
				clearTimeout(this.loadProductsTimer);
				this.loadProductsTimer = setTimeout(() => {
					axios
						.get(API_BASE_URL + '/api/products', {
							params: {
								page: this.page,
								limit: this.productsPerPage,
								categoryId: this.filterCategoryId,
								minPrice: this.filterPriceFrom,
								maxPrice: this.filterPriceTo,
								colorId: this.filterColorId
							}
						})
						.then(response => this.productsData = response.data)
						.catch(() => this.productsLoadingFailed = true)
						.then(() => this.productsLoading = false)
					;
				}, 100);
			}
		},
		created() {
			this.loadProducts();
		}
	}
</script>

