<template>
	<main class="content container">
		<div class="content__top">
			<ul class="breadcrumbs">
				<li class="breadcrumbs__item">
					<router-link class="breadcrumbs__link" :to="{name: 'main'}">
						Каталог
					</router-link>
				</li>
				<li class="breadcrumbs__item">
					<router-link class="breadcrumbs__link" :to="{name: 'cart'}">
						Корзина
					</router-link>
				</li>
				<li class="breadcrumbs__item">
					<a class="breadcrumbs__link">
						Оформление заказа
					</a>
				</li>
			</ul>

			<h1 class="content__title">
				Корзина
			</h1>
			<span class="content__info">
				{{ $store.state.cartProducts.length }} товара
			</span>
		</div>

		<div v-if="orderIsSubmiting" class="loader" />

		<section class="cart">
			<form class="cart__form form" action="#" method="POST" @submit.prevent="order">
				<div class="cart__field">
					<div class="cart__data">
						<base-form-text v-model="formData.name" :error="formError.name" title="ФИО" placeholder="Введите ваше полное имя" />
						<base-form-text v-model="formData.address" :error="formError.address" title="Адрес доставки" placeholder="Введите ваш адрес" />
						<base-form-text v-model="formData.phone" :error="formError.phone" title="Телефон" placeholder="Введите ваш телефон" />
						<base-form-text v-model="formData.email" :error="formError.email" title="Email" placeholder="Введи ваш Email" />
						<base-form-textarea v-model="formData.comment" :error="formError.comment" title="Комментарий к заказу" placeholder="Ваши пожелания" />
					</div>

					<div class="cart__options">
						<h3 class="cart__title">Доставка</h3>
						<ul class="cart__options options">
							<li class="options__item">
								<label class="options__label">
									<input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
									<span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
								</label>
							</li>
							<li class="options__item">
								<label class="options__label">
									<input class="options__radio sr-only" type="radio" name="delivery" value="500">
									<span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
								</label>
							</li>
						</ul>

						<h3 class="cart__title">Оплата</h3>
						<ul class="cart__options options">
							<li class="options__item">
								<label class="options__label">
									<input class="options__radio sr-only" type="radio" name="pay" value="card">
									<span class="options__value">
                    Картой при получении
                  </span>
								</label>
							</li>
							<li class="options__item">
								<label class="options__label">
									<input class="options__radio sr-only" type="radio" name="pay" value="cash">
									<span class="options__value">
                    Наличными при получении
                  </span>
								</label>
							</li>
						</ul>
					</div>
				</div>

				<OrderCartBlock :items="products" :totalPrice="totalPrice">
					<button class="cart__button button button--primery" type="submit">
						Оформить заказ
					</button>
				</OrderCartBlock>
				<div v-if="formErrorMessage" class="cart__error form__error-block">
					<h4>Заявка не отправлена!</h4>
					<p>{{ formErrorMessage }}</p>
				</div>
			</form>
		</section>
	</main>
</template>

<script>
	import BaseFormText from "@/components/BaseFormText.vue";
	import BaseFormTextarea from "@/components/BaseFormTextarea.vue";
	import axios from "axios";
	import {API_BASE_URL} from "@/config";
	import CartItem from "@/components/CartItem.vue";
	import {mapGetters} from "vuex";
	import NumberFormat from "../helpers/numberFormat";
	import OrderCartBlock from "@/components/OrderCartBlock.vue";

	export default {
		components: {OrderCartBlock, CartItem, BaseFormText, BaseFormTextarea},
		data() {
			return {
				formData: {
					// name: '213123dwd',
					// address: '12312dewdfrf34fg',
					// phone: '+79039373358',
					// email: '12e3e@e3e.ru',
					// comment: '3'
				},
				formError: {},
				formErrorMessage: '',
				orderIsSubmiting: false
			}
		},
		computed: {
			...mapGetters({
				products: 'cartDetailProducts',
				totalPrice: 'cartTotalPrice'
			})
		},
		filters: {NumberFormat},
		methods: {
			order() {
				this.formError = {};
				this.formErrorMessage = '';
				this.orderIsSubmiting = true;

				return (new Promise(resolve => setTimeout(resolve, 3000)))
					.then(() => {
						return axios
							.post(API_BASE_URL + '/api/orders', {
								...this.formData
							}, {
								params: {
									userAccessKey: this.$store.state.userAccessKey
								}
							})
							.then(response => {
								this.$store.commit('resetCart');
								this.$store.commit('updateOrderInfo', response.data);
								this.$router.push({
									name: 'orderInfo',
									params: {
										id: response.data.id
									}
								});
							})
							.catch(error => {
								this.formError = error.response.data.error.request || {};
								this.formErrorMessage = error.response.data.error.message || '';
							})
							.then(() => this.orderIsSubmiting = false)
						;
					})
				;
			}
		}
	}
</script>

<style>
.loader {
	background: url("../public/img/spinner.gif") 50% 50% no-repeat rgba(255,255,255,0.9);
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: 9999;
}
</style>